/*     
 (    ,     )
       "js-open-modal"   data-modal="1" (  )
     "js-overlay-modal"
     data-modal="1" (  )
      "js-close-modal" 
         ,    
     

        :
 .is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
 */

/*     <body>    bodyScrollLock
    <body> <script src="./js/bodyScrollLock.js"></script>,   
 import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock'
(       bodyScrollLock.disableBodyScroll  disableBodyScroll, ) */

//         (  )
const modalButtons = document.querySelectorAll('.js-open-modal');
const overlaysArr = document.querySelectorAll('.js-overlay-modal');
const closeButtons = document.querySelectorAll('.js-close-modal');

/*    */
modalButtons.forEach(function (item) {
  item.addEventListener('click', function (e) {
    /*    .      -. 
        ,  .  . */
    e.preventDefault();

    /*           data-modal
              ,  ,    . */
    const modalId = item.getAttribute('data-modal');
    const modalElem = document.querySelector('[data-modal="' + modalId + '"]:not(button):not(a)');
    const overlay = modalElem.closest('.js-overlay-modal');

    /*  ,    ,   is-hidden
       ,   ,     <body>. */
    overlay.classList.remove('is-hidden');
    document.addEventListener('keydown', onPressEscape);
    bodyScrollLock.disableBodyScroll(document.body);
  }); // end click
}); // end foreach

closeButtons.forEach(function (item) {
  item.addEventListener('click', function (e) {
    //           <body>
    const overlay = item.closest('.js-overlay-modal');
    overlay.classList.add('is-hidden');
    if (!item.classList.contains('js-open-modal')) {
      document.removeEventListener('keydown', onPressEscape);
      bodyScrollLock.enableBodyScroll(document.body); // added
    }
  }); // end foreach

  //               
  overlaysArr.forEach(item => {
    item.addEventListener('click', function (e) {
      if (e.target === e.currentTarget) {
        item.classList.add('is-hidden');
        document.removeEventListener('keydown', onPressEscape);
        bodyScrollLock.enableBodyScroll(document.body); // added
      }
    });
  });
}); // end foreach

//       Esc   
function onPressEscape(e) {
  if (e.key === 'Escape') {
    //     
    const overlay = document.querySelector('.js-overlay-modal:not(.is-hidden)');
    overlay.classList.add('is-hidden');
    bodyScrollLock.enableBodyScroll(document.body); // added
  }
}
